import { Component, OnInit, ViewChild } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { RoutingService } from 'src/app/shared/services/routing.service';
import { MatSnackBar } from '@angular/material';
import { ProductManagementService } from 'src/app/shared/services/product-management.service';
import { CommonService } from './../../../shared/services/common-service.service';
import { SharedProductDataService } from 'src/app/shared/services/share-data/shared-product-data.service';

@Component({
  selector: 'haribol-product-create',
  templateUrl: './product-create.component.html',
  styleUrls: ['./product-create.component.scss']
})
export class ProductCreateComponent implements OnInit {
  public productForm: FormGroup;
  public categorys: Array<any> = [];
  public weighingTypes: Array<any> = [];
  public nonSellingProducts: Array<any> = [];
  public countrys: Array<any> = [];
  public cities: Array<any> = [];
  public states: Array<any> = [];
  public identityTypes: Array<any> = [];
  public isUpdate: boolean;
  @ViewChild('fileInput') fileInput;
  constructor(
    public fb: FormBuilder,
    public routingService: RoutingService,
    public productservice: ProductManagementService,
    public commonService: CommonService,
    public sharedProductDataService: SharedProductDataService) { }

  ngOnInit() {
    this.getUtils();
    // productForm intiate formgroup
    this.productForm = this.fb.group({
      name: ['', Validators.required],
      code: ['', Validators.required],
      basePrice: [''],
      country: [''],
      city: [''],
      categoryId: ['', Validators.required],
      uom: [null],
      quantity: [''],
      description: [''],
      image: [''],
      forSale: [0],
      subscriptable: [1],
      dispensibleProduct: [null],
      hasDispensibleContainer: [0]
    });

    /**
     * Valuechanges subscribe forSale and currentUser$
     */
    this.productForm.get('forSale').valueChanges.subscribe(forsale => {
      if (!forsale) {
        this.productForm.get('dispensibleProduct').setValue(null);
      }
    });
    this.sharedProductDataService.currentUser$.subscribe((data) => {
      if (data) {
        this.isUpdate = true;
        this.dataAssignToForm(data);
      } else {
        this.isUpdate = false;
      }
    });

  }
  /**
   * Get services categorys, uoms, identityTypes, getNonSellingProducts
   */
  public getUtils() {
    this.commonService.getUomList().subscribe((res: any) => {
      this.weighingTypes = res;
    });
    this.commonService.getIdentityTypes().subscribe((res: any) => {
      this.identityTypes = res;
    });
    this.productservice.getCategoryList().subscribe(res => {
      this.categorys = res;
      console.log('THIS CATEGORYS ', this.categorys);
    });
    this.productservice.getNonSellingProducts().subscribe((res) => {
      this.nonSellingProducts = res;
    });
    this.commonService.getAllCountrys().subscribe(res => {
      this.countrys = res;
      console.log(this.countrys);
      this.productForm.get('country').setValue(res[0].code);
    });
    this.commonService.getRegions().subscribe(regions => {
      this.cities = regions;
    });
  }
  /** Save product */
  save() {
    this.commonService.validate(this.productForm);
    if (this.productForm.valid) {
      const productObj = this.productForm.value;
      productObj.forSale = productObj.forSale ? 1 : 0;
      productObj.hasDispensibleContainer = productObj.hasDispensibleContainer ? 1 : 0;
      productObj.subscriptable = productObj.subscriptable ? 1 : 0;
      this.productservice.addProduct(productObj).subscribe(res => {
        this.commonService.showSnackBar('Saved!!', 'Success');
        this.routingService.goToPage('product');
      }, () => {
        this.commonService.showSnackBar('Please fill mandatory fields', 'Failed Save');
      });
    }
  }
  /** update product */
  saveUpdate() {
    this.commonService.validate(this.productForm);
    if (this.productForm.valid) {
      const productObj = this.productForm.value;
      productObj.forSale = productObj.forSale ? 1 : 0;
      productObj.hasDispensibleContainer = productObj.hasDispensibleContainer ? 1 : 0;
      productObj.subscriptable = productObj.subscriptable ? 1 : 0;
      this.productservice.updateProduct(productObj).subscribe(res => {
        this.commonService.showSnackBar('Saved!!', 'Update Success');
        this.routingService.goToPage('product');
      }, () => {
        this.commonService.showSnackBar('Please fill mandatory fields', 'Failed update');
      });
    }
  }
  /** Redirect to product list page */
  redirectTo(page) {
    this.routingService.goToPage(page);
  }
  /** Update the productForm based on sharedService data */
  dataAssignToForm(data) {
    this.productForm.get('name').setValue(data.name);
    this.productForm.get('code').setValue(data.code);
    this.productForm.get('basePrice').setValue(data.basePrice);
    this.productForm.get('country').setValue(data.country);
    this.productForm.get('categoryId').setValue(data.categoryId);
    this.productForm.get('uom').setValue(data.uom);
    this.productForm.get('quantity').setValue(data.quantity);
    this.productForm.get('description').setValue(data.description);
    this.productForm.get('forSale').setValue(data.forSale);
    this.productForm.get('city').setValue(data.regions[0]);
    this.productForm.get('subscriptable').setValue(data.subscriptable);
    this.productForm.get('dispensibleProduct').setValue(data.dispensibleProduct);
    this.productForm.get('hasDispensibleContainer').setValue(data.hasDispensibleContainer);
    this.productForm.updateValueAndValidity({ onlySelf: true });
  }

}
