import { Injectable } from '@angular/core';
import { BehaviorSubject } from 'rxjs/internal/BehaviorSubject';

@Injectable({
  providedIn: 'root'
})
export class SharedProductDataService {
  private productDataSource = new BehaviorSubject(false);
  currentUser$ = this.productDataSource.asObservable();

  constructor() { }

  changeData(data: any) {
    this.productDataSource.next(data);
  }
}
