import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { Product } from 'src/app/core/interface/product';
import { environment } from 'src/environments/environment';
import { map } from 'rxjs/internal/operators/map';

@Injectable({
  providedIn: 'root'
})
export class ProductManagementService {

  constructor(public http: HttpClient) { }

  getProducts(): Observable<Product> {
    return this.http.get(environment.baseUrl + environment.product.url).pipe(
      map((res: any) => res.data)
    );
  }
  addProduct(data): Observable<Product> {
    return this.http.post(environment.baseUrl + environment.product.path, data).pipe(
      map((res: any) => res.data)
    );
  }
  updateProduct(data): Observable<Product> {
    return this.http.put(environment.baseUrl + environment.product.path, data).pipe(
      map((res: any) => res.data)
    );
  }
  getCategoryList(): Observable<any> {
    return this.http.get(environment.baseUrl + environment.product.category).pipe(
      map((res: any) => res.data)
    );
  }
  deleteProduct(id: any): any {
    return this.http.delete(environment.baseUrl + environment.product.url + '?productId=' + id).pipe(
      map((res: any) => res.data)
    );
  }
  getNonSellingProducts() {
    return this.http.get(environment.baseUrl + environment.product.nonselling).pipe(
      map((res: any) => res.data)
    );
  }
}
