import { Component, OnInit } from '@angular/core';
import { RoutingService } from 'src/app/shared/services/routing.service';
import { ProductManagementService } from './../../shared/services/product-management.service';
import * as _ from 'lodash';
import { SharedProductDataService } from 'src/app/shared/services/share-data/shared-product-data.service';
import { CommonService } from 'src/app/shared/services/common-service.service';

@Component({
  selector: 'haribol-product',
  templateUrl: './product.component.html',
  styleUrls: ['./product.component.scss']
})
export class ProductComponent implements OnInit {
  public productData: any = [];
  public searchVal: any;
  public productLength: number;
  constructor(
    public routingService: RoutingService,
    public productService: ProductManagementService,
    public sharedProductDataService: SharedProductDataService,
    public commonService: CommonService) { }

  ngOnInit() {
    this.productLength = 0;
    this.init();
  }
  /**
   * Init method for getting all products
   */
  init() {
    this.productService.getProducts().subscribe((result: any) => {
      const data = _.sortBy(_.flatten(_.map(result, 'products')), ['productId']);
      this.productData = data;
      this.productLength = result.length;
    });
  }
  /**
   * getSearchInput method for assign searchValue
   */
  public getSearchInput(val) {
    this.searchVal = val;
  }
  /**
   * addNewBtnClick method will update sharedProductDadta to null
   * And also it will redirect to product create page
   */
  public addNewBtnClick() {
    this.sharedProductDataService.changeData(null);
    this.routingService.goToPage('product', 'create');
  }
  /**
   * editBtnClick method will update sharedProductDadta with product object
   * And also it will redirect to product update page
   */
  public editBtnClick(product) {
    this.sharedProductDataService.changeData(product);
    this.routingService.goToPage('product', 'update', product.productId);
  }
  /**
   * removeBtnClick method will remove product object
   */
  removeBtnClick(product) {
    this.productService.deleteProduct(product.productId).subscribe(res => {
      this.commonService.showSnackBar('Successfully removed Product!!', 'Deleted Product');
      this.init();
    });
  }
}
